				<view class="cu-item" :class="indexs==TabCurs?'text-green cur':''" v-for="(item,indexs) in tab" :key="indexs" @tap="searchSelect"
				 :data-id="indexs">
					<text :style="indexs==TabCurs?'background: #FC3F78;color: white;padding: 2px 10px 2px 10px;border-radius: 16px;margin-right: -10px':'background: #ffffff;color: #333333;margin-right: -10px'">
						{{item.name}}
					</text>
				</view>
			</scroll-view>
			<!--#endif-->
		</view>
		<view style="height: 1px;background: grey;width: 100%"></view>
		<view class="index-content" style="padding-top: 90px;">
			<view class="index-coupon has-bg-white has-pd-10 top_30">
				<view class="goods-list" v-if="couponlist.length > 0">
					 <orange-goods-card-home
					         v-for="(item,index) in couponlist"
					         :index="index%2"
					         :tkmoney='item.tkmoney'
							 :isEnable="isEnable"
							 :tkmoneys='item.tkmoneys'
					         :itemid="item.itemid"
					         :logo="item.shoptype === 1? logoMao : logoTaoBao"
					         :is-invitation="isInvitation"
					         :itempic="item.itempic+'_310x310.jpg'"
					         :itemtitle="item.itemtitle"
					         :itemprice='"Â¥"+item.itemprice'
					         :itemsale='item.itemsale'
					         :itemendprice='""+item.itemendprice'
					         :couponmoney="item.couponmoney"
							 :item="item"
					 ></orange-goods-card-home>
				</view>
			</view>
			<!-- æ¬æµ®ä¸æ -->
			<view class="scroll_top" @tap="topScrollTap" v-bind:class="[scrollTop ? 'active' : '','']" style="bottom: 56px;">
				<text class="iconfont icon-shangla"></text>
			</view>


			<!-- å è½½æ´å¤æç¤º -->
			<view class="s-col is-col-24" v-if="couponlist.length > 0">
				<load-more :loadingType="loadingType" :contentText="contentText"></load-more>
			</view>
			<!-- å è½½æ´å¤æç¤º -->
			<empty v-if="couponlist.length === 0" des="ææ æ°æ®" show="false"></empty>

			<!-- #ifdef H5 -->
			<!--  <view class="scroll_top active " @click="addPublisher()"
                  style="bottom: 150px;width: 100px;right: -12px;padding-right: 10px">
                <text>
                    <text class="cuIcon cuIcon-down"></text>
                    ä¸è½½APP
                </text>
            </view> -->
			<!--#endif-->
		</view>
	</view>
</template>

<script>
	export default {
		onShareAppMessage(res) {
			return {
				title: 'è´­ç©åé¢å¸ï¼ä¸å¹´çä¸å',
				path: '/pages/index/index'
			}
		},
		data() {
			return {
				logoMao: '../../static/img/mao.png',
				logoTaoBao: '../../static/img/taobao.png',
				isEnable:'å¦',
				tab: [
					{ name: 'ç­é', position: 1, total: 0, data: [] },
					{ name: 'ç»¼å', position: 2, total: 0, data: [] },
					{ name: 'ææ°', position: 3, total: 0, data: [] },
					{ name: 'é«ä½£', position: 4, total: 0, data: [] },
					{ name: 'ä½ä»·', position: 5, total: 0, data: [] },
				],
				category: [
					{ name: 'å¨é¨', positon: 0},
					{ name: 'å¥³è£', positon: 1},
					{ name: 'ç·è£', positon: 2},
					{ name: 'åè¡£', positon: 3},
					{ name: 'ç¾å¦', positon: 4},
					{ name: 'éé¥°', positon: 5},
					{ name: 'éå', positon: 6},
					{ name: 'ç®±å', positon: 7},
					{ name: 'å¿ç«¥', positon: 8},
					{ name: 'æ¯å©´', positon: 9},
					{ name: 'å±å®¶', positon: 10},
					{ name: 'ç¾é£', positon: 11},
					{ name: 'æ°ç ', positon: 12},
					{ name: 'å®¶çµ', positon: 13},
					{ name: 'å¶ä»', positon: 14},
					{ name: 'è½¦å', positon: 15},
					{ name: 'æä½', positon: 16},
					{ name: 'å® ç©', positon: 17},
				],
				TabCur: 0,
				TabCurs: 0,
				scrollLeft: 0,
				scrollLefts: 0,
				couponlist: [],
				page: 1,
				cid: 0,
				type: 1,
				isInvitation: 0,
				position: 1,
				genderKey: "gender",
				loadingType: 0,
				deviceValue:'',
				scrollTop: false,
				contentText: {
					contentdown: "ä¸ææ¾ç¤ºæ´å¤",
					contentrefresh: "æ­£å¨å è½½...",
					contentnomore: "æ²¡ææ´å¤æ°æ®äº"
				}
			}
		},
		onLoad: function(e) {
			let isEnable = this.$queue.getData("isEnable")
			if (isEnable) {
				this.isEnable = isEnable;
			}
			let deviceValue = this.$queue.getData("DeviceNo");
			if(deviceValue){
				this.deviceValue = deviceValue;
			}
			let isInvitation = this.$queue.getData("isInvitation");
			if(isInvitation){
				this.isInvitation = isInvitation;
			}
			if (e.title && e.type) {
				this.type = e.type;
				uni.setNavigationBarTitle({
					title: e.title
				});
				uni.showLoading({
					title: 'å è½½ä¸­...'
				});
			} else {
				uni.navigateBack()
			}
		},
		onPageScroll: function(e) {
			this.scrollTop = e.scrollTop > 200;
		},
		methods: {
			addPublisher() {
				if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) { //å¤æ­iPhone|iPad|iPod|iOS
					uni.showModal({
						title: "ä¸è½½æç¤º",
						content: 'è¹æAPPæ­£å¨ä¸æ¶ä¸­è¯·åå³æ³¨å¬ä¼å·ä½¿ç¨å¦',
						showCancel: true,
						success: (e) => {
							if (e.confirm) {
								uni.navigateTo({
									url: '/pages/member/erweima'
								});
							}
						}
					});
				} else if (/(Android)/i.test(navigator.userAgent)) { //å¤æ­Android
					window.location.assign("https://fir.vip/d/5yilc")
				} else { //pc
					window.location.assign(this.$Url)
				}
			},
			searchSelect(e) {
				this.TabCurs = e.currentTarget.dataset.id;
				this.position = this.tab[e.currentTarget.dataset.id].position;
				this.scrollLefts = (e.currentTarget.dataset.id - 1) * 60;
				this.page = 1;
				uni.showLoading({
					title: 'å è½½ä¸­...'
				});
				//#ifdef H5
				uni.pageScrollTo({
					scrollTop: 0,
					duration: 300
				});
				//#endif
				this.loadCouponList("Refresh");
			},
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.cid = this.category[this.TabCur].positon;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
				this.page = 1;
				uni.showLoading({
					title: 'å è½½ä¸­...'
				});
				//#ifdef H5
				uni.pageScrollTo({
					scrollTop: 0,
					duration: 300
				});
				//#endif
				this.loadCouponList("Refresh");
			},
			topScrollTap: function() {
				uni.pageScrollTo({
					scrollTop: 0,
					duration: 300
				});
			},

			loadCouponList: function(type) {
				let materialId;
				let apiUrl = this.$Api.common.couponlist;
				if(this.cid === 0){
					apiUrl = this.$Api.common.couponbestlist;
					materialId = "6708"; 
				}
				
				console.log("----DENG-Begin---");
				console.log("----apiUrl----" + apiUrl);
				console.log("----cid----" + this.cid);
				console.log("----type----" + this.type);
				console.log("----this.page----" + this.page);
				console.log("----this.deviceValue----" + this.deviceValue);
				console.log("----DENG-End---");
				
				this.loadingType = 1;
				this.$Request.get(apiUrl, {
					ColumnId: this.cid,
					MaterialId: materialId,
					PageNo: this.page,
					Position: this.position,
					DeviceValue: this.deviceValue
				}).then(res => {
					console.log(res);
					this.loadingType = 0;
					if (res.Code === 1) {
						if (this.page === 1) {
							this.couponlist = [];
						}
						res.Message.forEach(d => {
							d.tkmoneys = (d.tkmoney * 0.7).toFixed(2);
							let grade = this.$queue.getData("grade");
							if (grade) {
								d.tkmoney = (d.tkmoney * parseFloat(grade)).toFixed(2);
							} else {
								d.tkmoney = (d.tkmoney * 0.3).toFixed(2);
							}
							d.itemsale = d.itemsale > 10000 ? 'å·²å® ' + (d.itemsale / 10000).toFixed(1) + 'ä¸' : 'å·²å® ' + d.itemsale;
							this.couponlist.push(d);
						})

					} else {
						this.loadingType = 2;
					}
					uni.hideLoading();
					if (type === "Refresh") {
						uni.stopPullDownRefresh(); // åæ­¢å·æ°
					}
				})
			},
			toGoodsInfo: function(e) {
				let relation_id = this.$queue.getData("relation_id");
				if (relation_id) {
					uni.navigateTo({
						url: "/pages/detail/goodsinfo?data=" + encodeURIComponent(JSON.stringify(e)) + "&relation_id=" + relation_id,
					})
				} else {
					uni.navigateTo({
						url: "/pages/detail/goodsinfo?data=" + encodeURIComponent(JSON.stringify(e)),
					})
				}
			},
		},

		onReady: function() {
			this.loadCouponList();
		},
		onReachBottom: function() {
			this.page = this.page + 1;
			this.loadCouponList();
		},
		onPullDownRefresh: function() {
			this.page = 1;
			this.loadCouponList("Refresh");
		}
	}
</script>

